@import '../common/variables.scss';
.page-header {
  background: rgba(29, 0, 0, 0.302);
  height: 65px;
}

.page-header__logo {
  position: absolute;
  z-index: 2;
}

.main-nav {
  position: relative;
  margin: 0 auto;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width:100%;
}

.main-nav__toggle {
  position: absolute;
  top: calc(50% - 12.5px);
  right: 0;
  z-index: 2;
  display: block;
  width: 50px;
  height: 25px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}

.main-nav--opened {
  background-color: #283645;
}

.main-nav--opened .main-nav__toggle {
  top: 0;
  right: 0;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 24px;
    left: 18px;
    width: 36px;
    height: 2px;
    background-color: #fff;
  }

  &::before {
    transform: rotate(45deg);
    box-shadow: none;
  }

  &::after {
    transform: rotate(-45deg);
  }

  &:active::before,
  &:active::after {
    background-color: rgba(255, 255, 255, 0.3);
  }

  img {
    display: none;
  }
}

.main-nav__list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.site-list__item {
  height: 60px;
  border-bottom: 1px solid #010101;
  border-top: 1px solid #485461;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

  a {
    font-size: 18px;
    font-weight: bold;
    text-transform: uppercase;
    line-height: 1.333;
    color: #fff;
    text-decoration: none;

    &:hover {
      color: #6d314c;
    }

    &:focus {
      color: #485562;
    }
  }
}

.main-nav.main-nav--closed {
  width: 87%;
}

.main-nav.main-nav--opened {
  .page-header__logo {
    margin-left: 6%;
  }

  .main-nav__toggle {
    margin-right: 6%;
  }
}

@media (max-width: $mobile-width-only) {

  .main-nav--closed .site-list__item {
    display: none;

    &--active {
      display: block;
    }
  }

  .main-nav--closed .site-list__item--active a {
    padding-right: 70px;
    padding-left: 70px;
    text-align: center;
  }

  .main-nav--closed .user-list__item {
    display: none;
  }

  .main-nav--opened .main-nav__wrapper {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 10;
    background-color: #283645;
    margin-top: 65px;
    width: 100%;
  }

  .main-nav--nojs .main-nav__toggle {
    display: none;
  }

  .main-nav--nojs .main-nav__wrapper {
    position: static;
    min-height: 0;
  }

  .main-nav--nojs .site-list__item {
    display: block;
  }

  .main-nav--nojs .site-list__item--active a {
    padding-right: 21px;
    padding-left: 21px;
    text-align: left;
  }

  .main-nav--nojs .user-list__item {
    display: block;
    background-color: #6a4d38;
  }
}

@media (min-width: $tablet-width) {
  .main-nav.main-nav--closed {
    width: 94%;
  }

  .page-header {
    height: 110px;
  }

  .main-nav--opened .main-nav__wrapper {
    margin-top: 110px;
  }

  .main-nav--opened .main-nav__toggle {
    top: 30%;
  }
}

@media (min-width: $desktop-width) {
  .main-nav.main-nav--closed {
    width: 80%;
  }

  .main-nav__wrapper {
    width: 80%;
    z-index: 2;
    display: flex;
    position: absolute;
    right: 0;
  }

  .main-nav__toggle {
    display: none;
  }

  .main-nav__list {
    flex-direction: row;
    width: 100%;
  }

  .site-list__item {
    border: none;
  }

  .main-nav__list.user-list {
    display: none;
  }

  .site-list__item a {
    font-size: 18px;
    text-transform: none;
    font-weight: normal;

    &:hover {
      color: #6d314c;
    }

    &:focus {
      color: #485562;
    }
  }

  .site-list__item:active {

    &::after {
      content: "";
      position: absolute;
      right: 0;
      bottom: -12px;
      left: 0;
      height: 2px;
      background: #ffffff;
      width: 25%;
    }
  }
  
}


